  .menu {
    height: 80px;
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    list-style: none;
    padding: 0 100px;
    background: #0f2027;
   
  }

  .menu li {
    height: 30%;
    font-size: 18px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    text-transform: uppercase;
    transition: all 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
    color: #fff;
  }

  .menu li::before,
.menu li::after {
  content: "";
  pointer-events: none;
  position: absolute;
}
  .menu1 li::before,
.menu1 li::after {
  border: 0px solid transparent;
  width: 0%;
  height: 0%;
  bottom: 0%;
  right: 0%;
}

/* 'before' sudo element animates the bottom and left borders */
.menu1 li::before {
  border-bottom-width: 2px;
  border-left-width: 2px;
}

/* 'after' sudo element animates the top and right borders */
.menu1 li::after {
  border-top-width: 2px;
  border-right-width: 2px;
}

.menu1 li:hover {
  color: #fff;
}

/* On Hover border color is changed from transparent to the #fff */
/* Width and a height of the sudo elements are increased from zero to 100% */
.menu1 li:hover::before,
.menu1 li:hover::after {
  border-color: #fff;
  transition: border-color 0.5s, width 0.08s, height 0.08s;
  width: 100%;
  height: 100%;
}

/* Delay is added to the height animation of the 'before' sudo element */
.menu1 li:hover::before {
  transition-delay: 0s, 0s, 0.3s;
}

/* Delay is added to the width animation of the 'after' sudo element */
.menu1 li:hover::after {
  transition-delay: 0s, 0.3s, 0s;
}
